import Vue from 'vue'
import VueRouter from 'vue-router'


import Inicio from '../views/Inicio.vue'
import Consultas_web from '../views/Consultas_web.vue'

import Productos_tendencia from '../views/Productos_tendencia.vue'
import Productos_natures_sunshine from '../views/Productos_natures_sunshine.vue'
import Productos_bio_naturista from '../views/Productos_bio_naturista.vue'
import Productos_natural_pluss from '../views/Productos_natural_pluss.vue'
import Productos_vidax from '../views/Productos_vidax.vue'
import Productos_dxn from '../views/Productos_dxn.vue'
import Productos_teoma from '../views/Productos_teoma.vue'
import Plantas_y_hierbas_medicinales from '../views/Plantas_y_hierbas_medicinales'


Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'inicio',
    component: Inicio
  },
  {
    path: '/consultas_web',
    name: 'consultas_web',
    component: Consultas_web
  },
  {
    path: '/productos_tendencia',
    name: 'productos_tendencia',
    component: Productos_tendencia
  },
  {
    path: '/productos_natures_sunshine',
    name: 'productos_natures_sunshine',
    component: Productos_natures_sunshine 
  },
  {
    path: '/productos_bio_naturista',
    name: 'productos_bio_naturista',
    component: Productos_bio_naturista
  },
  {
    path: '/productos_natural_pluss',
    name: 'productos_natural_pluss',
    component: Productos_natural_pluss
  },
  {
    path: '/productos_vidax',
    name: 'productos_vidax',
    component: Productos_vidax 
  },
  {
    path: '/productos_dxn',
    name: 'productos_dxn',
    component: Productos_dxn
  },
  {
    path: '/productos_teoma',
    name: 'productos_teoma',
    component: Productos_teoma
  },  
  {
    path: '/plantas_y_hierbas_medicinales',
    name: 'Plantas_y_hierbas_medicinales',
    component: Plantas_y_hierbas_medicinales
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
  linkActiveClass: "active",
  linkExactActiveClass: "active"
})

export default router
